"use client";

import { useEffect } from "react";
import { ThemeProvider } from "next-themes";
import "@/i18n";
import { SessionLang, ThemeMode } from "@/utils/services/app.event";
import AppStorage, { THEME } from "@/utils/services/app.storage";
import { useTranslation } from "react-i18next";

export function ClientProvider({ children }: { children: React.ReactNode }) {
    const { i18n } = useTranslation();

    useEffect(() => {
        // Sync i18n with signal
        i18n.changeLanguage(SessionLang.value);
    }, [SessionLang.value, i18n]);

    useEffect(() => {
        // Sync theme with storage on mount
        const storedTheme = AppStorage.getData(THEME);
        if (storedTheme) {
            ThemeMode.value = storedTheme;
        }
    }, []);

    return (
        <ThemeProvider attribute="data-theme" defaultTheme="light" enableSystem={false}>
            {children}
        </ThemeProvider>
    );
}
